<template>
    <div class="discount">
       <h4>{{ count }}초 안에 결제하면 20% 할인</h4>
    </div>
</template>

<script>


export default {
    name: 'DiscountAd',
    data(){
        return{
            count : 5
        }
    },
    
    mounted() {
        var a = setInterval(()=>{
                this.count--;
                console.log(this.count)

                if(this.count == 0){
                    clearInterval(a);
                }
            }, 1000)
            console.log(this.count)
    },
}

</script>

