<template>

  <!-- 모달 -->
  <transition name="fade">
    <Modal  @closeModal="모달창열렸니 = false" 
        :원룸들="원룸들" :roomNum="roomNum" :모달창열렸니="모달창열렸니"></Modal>
  </transition>

  <!-- 네비 -->
  <div class="menu">
    <a v-for="(name) in menu" :key="name" >{{ name }}</a>
  </div>


  <!-- 홍보UI -->
  <div style="padding: 10px;">
    <Discount v-if="showDiscount == true"></Discount>
  </div>

  <button @click="priceSort">가격순정렬</button>
  <button @click="priceReSort">가격역순정렬</button>
  <button @click="titleSort">가나다순정렬</button>
  <button @click="priceFilter">50만원이하</button>
  <button @click="sortBack">되돌리기</button>


  <!-- 컨텐츠 -->
  <CardList @openModal = "모달창열렸니 = true; roomNum = $event"
            :원룸들="원룸들" :roomNum="roomNum"></CardList>
</template>



<script>
// Vue의 라이프사이클을 어디다 쓰냐면 까지

import onerooms from './assets/oneroom'
import discount from './DiscountAd.vue'
import modal from './Modal.vue'
import cardList from './CardList.vue'

export default {
  name: 'App',
  
  data() {
    return {
      showDiscount : true,
      원룸들원본 : [...onerooms],
      roomNum : 0,
      원룸들 : onerooms,
      모달창열렸니 : false,
      신고수 : [0, 0, 0],
      menu : ['HOME', 'SHOP', 'ABOUT'],
      style : 'color : blue',
      products : ['뚝섬원룸', '성수원룸', '건대원룸'],
    }
  },

  methods: {
    increse(i){
      this.신고수[i] += 1;
    },
    priceSort(){
      this.원룸들.sort(function(a, b){
        return a.price - b.price;
      })
    },
    priceReSort(){
      this.원룸들.sort(function(a, b){
        return b.price - a.price;
      })
    },
    titleSort(){
      this.원룸들.sort(function(a, b){
        return a.title.localeCompare(b.title);
      })
    },
    priceFilter(){
      this.원룸들 = this.원룸들.filter(function(a){
        return a.price <= 500000;
      })
    },
    sortBack(){
      this.원룸들 = [...this.원룸들원본];
    },
  },

  components: {
    Discount : discount,
    Modal : modal,
    CardList : cardList,
  },

  mounted(){
    setTimeout(()=>{
      this.showDiscount = false
    }, 5000)
  }

}
</script>

