<template>
  <div class="black-bg" v-if="모달창열렸니 == true">
    <div class="white-bg">
      <h4>{{ 원룸들[roomNum].title }}</h4>
      <img :src="원룸들[roomNum].image">
      <p>{{ 원룸들[roomNum].content }}</p>
      <!-- <input @input="month = $event.target.value"> -->
      <input v-model="month">
      <input v-model="month" type="range" min="1" max="12" value="1">
      <p>{{ month }} 개월 : {{ 원룸들[roomNum].price * month}}원</p>
      <button @click="$emit('closeModal')">exit</button>
    </div>
  </div>
</template>

<script>
export default {
    name: 'DiscountAd',
    data(){
      return{
        month : 1,
      }
    },
    watch : {
      month(a){
        if(a > 12){
          alert("12개월 이하 입력 가능")
        }
        if(isNaN(a) == true){
          alert("숫자만 입력 입력 가능")
          this.month = 1;
        }
        if(a < 3){
          alert("3개월 이상부터 가능")
          this.month = 3;
        }
      }
    },
    props : {
      원룸들 : Object,
      roomNum : Number,
      모달창열렸니 : Boolean
    }
}
</script>

